//定义构造器
const COSFN = function (params, callback) {
    this.$Message = Vue.prototype.$Message;
    this.errorCatchModal = Vue.prototype.errorCatchModal;
    if (!params) return false;
    this.params = params;
    this.options = {};
    this.sha256 = sha256;
    this.items = items; //所有用到的itemID
    this.init(callback);
}
COSFN.prototype.showLoading = function (cb) {
    store.commit('app/showLoading');
}
COSFN.prototype.hideLoading = function (cb) {
    store.commit('app/hideLoading');
}
COSFN.prototype.errorModal = function (content, onOk, okText) {
    var arr = content.split(';');
    var text = '';
    arr.forEach(item => {
        if (item) text += !!text ? ('；<br>' + item) : item;
    })
    let options = {
        title: '提示',
        okText: okText || '确 定',
        content: text,
    };
    if (onOk) options.onOk = onOk;
    Vue.prototype.$Modal.error(options);
}
//初始化新建内部COS
COSFN.prototype.makeCos = function (cb) {
    let token = this.token;
    let _this = this;
    this.cos = new COS({
        getAuthorization: (options, callback) => {
            const data = this.options.tmpSecretData;
            this.getSign((data) => {
                callback({
                    TmpSecretId: data.tmpSecretId,
                    TmpSecretKey: data.tmpSecretKey,
                    XCosSecurityToken: data.sessionToken,
                    ExpiredTime: data.expiredTime,
                });
            })
        }
    })
    cb && cb(token);
}
//初始化获取token
COSFN.prototype.init = function (cb) {
    if (this.token && this.cos) return;
    this.showLoading();
    Vue.prototype.$http.post(urls.getAttachToken, this.params).then((res) => {
        this.hideLoading();
        let result = res.data;
        if (!result.success) {
            this.token = undefined;
            return this.$Message.error(result.errorMessage || '上传token获取失败');
        }
        let token = result.data.token;
        this.token = token;
        if (this.options.Bucket && this.options.Region) {
            this.makeCos(cb);
        } else {
            this.getSign((data) => {
                this.options.downloadHostName = data.downloadHostName;
                this.options.picHostName = data.picHostName;
                this.options.tencentCloudHostName = data.tencentCloudHostName;
                this.options.remotePath = data.remotePath;
                this.options.Bucket = data.bucketName;
                this.options.Region = data.region;
                this.options.qcloudCosUrl = data.tencentCloudHostName;
                this.makeCos(cb);
            })
        }
    }).catch((error) => {
        this.hideLoading();
        this.$Message.error(error || '上传token请求出错');
        this.token = undefined;
    })
};
//获取密钥
COSFN.prototype.getSign = function (cb) {
    this.showLoading();
    Vue.prototype.$http.post(urls.getAttachCloudSign, {
        "token": this.token
    }).then((res) => {
        this.hideLoading();
        let result = res.data;
        if (!result.success) return this.$Message.error(result.errorMessage || '上传密钥获取失败');
        let data = result.data;
        cb && cb(data)
    }).catch((error) => {
        this.hideLoading();
        this.errorCatchModal('上传密钥请求出错：', error);
    })
};
//保存到云盘，返回带下载地址的信息
COSFN.prototype.saveItemToYun = function (targetObj, callback) {
    if (!targetObj) return false;
    let obj = {
        "token": this.token,
        "items": [{
            "itemID": targetObj.itemID,
            "title": this.items[targetObj.itemID].title,
            "files": [{
                "name": targetObj.name,
                "remoteFileUrl": targetObj.remoteKey
            }]
        }]
    }
    this.showLoading();
    Vue.prototype.$http.post(urls.updateAttachItemAndQuery, obj).then((res) => {
        this.hideLoading();
        let result = res.data;
        if (!result.success) {
            callback && callback(false);
            return this.$Message.error(result.errorMessage || '保存云盘附件失败');
        }
        let data = result.data;
        callback && callback(data);
    }).catch((error) => {
        this.hideLoading();
        callback && callback(false);
        this.errorCatchModal('保存云盘附件出错：', error);
    })
}
//转换文件数据，返回hash名称和Key和bloburl
COSFN.prototype.convertBlobInfo = function (file, callback) {
    if (!file) return false;
    const indexOf = file.name.lastIndexOf('.');
    let ext = '';
    if (indexOf > 0) ext = file.name.substring(indexOf + 1).toLowerCase();
    this.readBlob({
        blob: file,
        type: "url",
        data: this,
        load: (e) => {
            const sha = this.sha256.create();
            const fileresult = e.target.result + new Date().getTime();
            sha.update(fileresult);
            const hash = sha.hex();
            const bloburl = e.target.result;
            const hashName = hash + '.' + ext;
            typeof callback == 'function' && callback({
                ext: ext,
                hash: hash,
                hashName: hashName,
                bloburl: bloburl,
                size: file.size
            })
        }
    })
}
