//计算基本信息的必填字段
export const getBaseField = (baseInfo) => {
    //必填   新车指导价没有算在内
    const needed = [
        "businessType", //业务类型
        "leaseType", //租赁属性
        "repaymentMode", //还款方式
        "repaymentFrequency", //还款频率
        "financePeriod", //融资期限
        "purchasePurpose", // 购车目的
        "carCommercialType", //车辆使用性质
        "isResourceCar", // 是否资源车
        "carType", //车辆类型  -->判断   二手车+7个
        "isReverseMortgage", //是否反贷  -->判断
        //新增三个必填字段
        "custPrimaryChannel",//客户一级渠道030100000线上 030100001线下
        "predictLicenseProvinceCode",//预计上牌省份
        "predictLicenseCityCode",//预计上牌城市
    ]
    let arr = [].concat(needed)
    const car1 = [
        "actualSellingPrice", //实际销售价 -----新车
    ]
    const car2 = [
        "mileage", //公里数（万公里）----二手车
        "carEstimatedPrice", //车辆预估价      ----二手车
        "vinNum", //车架号（VIN码） ----二手车  文档字段名：车架VIN号码？
        "productionDate", //出厂日期        ----二手车
        "initialDate", //初登日期        ----二手车
        "provinceCode", //当前牌照第一位        ----二手车
        "cityCode", //当前牌照第一位第二位        ----二手车
        "licenseSequence", //当前牌照尾数        ----二手车
        "licenseNum", //当前牌照        ----二手车 牌照完整组合字段
        "carEvaluationAgency", //二手车评估机构  ----二手车
    ]
    const car1r1 = [
        "carInvoiceDate", //车辆开票日期   ----新车 && 反贷
    ]
    const car2r1 = [
        "carTransferDate", //车辆过户完成日期----二手车 && 反贷
    ]
    const car3 = [
        "transferTimes",//车辆过户次数  -- 车抵贷
        "compulsoryInsuranceExpiryDate", //交强险失效日  -- 车抵贷
    ];
    if (yx.isSecondCar(baseInfo.carType))
        arr = arr.concat(car2);
    else
        arr = arr.concat(car1);
    //车抵贷
    if (yx.isChedidai(baseInfo.businessType)) {
        arr = arr.concat(car3);
    }

    if (yx.isSecondCar(baseInfo.carType) && yx.isAntiLoan(baseInfo.isReverseMortgage))
        arr = arr.concat(car2r1);
    else if (yx.isAntiLoan(baseInfo.isReverseMortgage))
        arr = arr.concat(car1r1);

    let finishedFields = [];
    let unFinishedFields = [];
    const numfields = ['mileage', 'actualSellingPrice', 'carEstimatedPrice', 'carGuidancePrice'];
    arr.forEach(field => {
        if (yx.isEmpty(baseInfo[field])) {
            unFinishedFields.push(field);
        } else {
            finishedFields.push(field);
        }
    })
    var obj = {
        totalFields: arr,
        finishedFields: finishedFields,
        unFinishedFields: unFinishedFields,
        total: arr.length,
        count: finishedFields.length
    }
    // console.log('baseInfo字数', obj);
    return obj;
}


//计算保险信息的必填字段
export const getItemField = (loan, itemInfo, baseInfo, financePeriodYear) => {
    var isGpsIncreaseItemDisabled = yx.isGpsIncreaseItemDisabled(loan);
    var isTheftItemDisabled = yx.isTheftItemDisabled(loan);
    var insuranceObj = getInsuranceField(itemInfo, baseInfo, financePeriodYear);
    var gpsObj = getGpsField(itemInfo, baseInfo, financePeriodYear);
    var theftObj = getTheftField(itemInfo, baseInfo, financePeriodYear, isTheftItemDisabled);
    var otherObj = getOtherField(itemInfo, baseInfo, financePeriodYear, isGpsIncreaseItemDisabled);
    var tt = insuranceObj.total + gpsObj.total + theftObj.total + otherObj.total;
    var cc = insuranceObj.count + gpsObj.count + theftObj.count + otherObj.count;

    var obj = {
        total: tt,
        count: cc
    };
    // console.log('item总字数', obj)
    return obj;

}
//计算保险信息的必填字段
export const getInsuranceField = (itemInfo, baseInfo, financePeriodYear) => {
    var arr = [], finishedFields = [], unFinishedFields = [];
    //是否有保险
    if (!itemInfo || !baseInfo || baseInfo.hasInsurance != "1" || !itemInfo.insuranceInfo) {
        return {
            totalFields: arr,
            finishedFields: finishedFields,
            unFinishedFields: unFinishedFields,
            total: arr.length,
            count: finishedFields.length
        }
    }
    var headArr = ["isFinanceInsurance"],//是否融
        neededArr = ["insuranceChannel", "insuranceMode"], //选择融时要出单地和商业险出单方式
        itemCarNeededArr = ["financeCode", "insuranceAmount"],//每一年中只有车船险时两个
        itemBusiNeededArr = ["financeCode", "insuranceKind", "insuranceAmount"];//每一年中选择商业险时三个
    let insuranceInfo = itemInfo.insuranceInfo;
    if (baseInfo.hasInsurance == "1" && !!insuranceInfo.isFinanceInsurance) {
        //选择了融或不融，都是填了
        arr = arr.concat(headArr);
        finishedFields.push(headArr[0]);
        if (insuranceInfo.isFinanceInsurance == "1") {
            //选择融时顶部两个字段
            arr = arr.concat(neededArr);
            if (yx.isEmpty(insuranceInfo.insuranceChannel)) {
                unFinishedFields.push(neededArr[0]);
            } else {
                finishedFields.push(neededArr[0]);
            }
            if (yx.isEmpty(insuranceInfo.insuranceMode)) {
                unFinishedFields.push(neededArr[1]);
            } else {
                finishedFields.push(neededArr[1]);
            }
            if (insuranceInfo.insuranceItemList && insuranceInfo.insuranceItemList.length) {
                insuranceInfo.insuranceItemList.forEach(item => {
                    //选择中包括商业险:只有交强险：没有选（没有必融项）
                    let narr = yx.isHasBusiIns(item) ? itemBusiNeededArr : !!item.financeCode ? itemCarNeededArr : [];
                    narr.forEach(field => {
                        arr.push(field + item.period)
                        if ((field == "insuranceAmount" && _.isNumber(item[field])) || !!item[field]) {
                            finishedFields.push(field + item.period)
                        } else {
                            unFinishedFields.push(field + item.period)
                        }
                    })
                })
            }

        }
    } else if (baseInfo.hasInsurance == "1" && !insuranceInfo.isFinanceInsurance) {
        //未选择
        arr = arr.concat(headArr);
        unFinishedFields.push(headArr[0]);
    }
    var obj = {
        totalFields: arr,
        finishedFields: finishedFields,
        unFinishedFields: unFinishedFields,
        total: arr.length,
        count: finishedFields.length
    };
    // console.log('保险的字数', obj)
    return obj;

}

//计算GPS信息的必填字段
export const getGpsField = (itemInfo, baseInfo, financePeriodYear) => {
    var arr = [], finishedFields = [], unFinishedFields = [];
    //是否有保险
    if (!itemInfo || !baseInfo || baseInfo.hasGps != "1" || !itemInfo.gpsInfo) {
        return {
            totalFields: arr,
            finishedFields: finishedFields,
            unFinishedFields: unFinishedFields,
            total: arr.length,
            count: finishedFields.length
        }
    }
    var gpsInfo = itemInfo.gpsInfo;
    var neededArr = ["gpsSupplier", "basePrice", "increasePrice", "increaseFinanceType"];//是否融,删除"custPrimaryChannel"
    arr = arr.concat(neededArr);
    var numfields = ['basePrice', 'increasePrice'];
    arr.forEach(field => {
        if (yx.isEmpty(gpsInfo[field])) {
            unFinishedFields.push(field)
        } else {
            finishedFields.push(field)
        }
    })
    var obj = {
        totalFields: arr,
        finishedFields: finishedFields,
        unFinishedFields: unFinishedFields,
        total: arr.length,
        count: finishedFields.length
    }
    // console.log('gps字数', obj)
    return obj;
}

//计算盗抢险信息的必填字段
export const getTheftField = (itemInfo, baseInfo, financePeriodYear, isTheftItemDisabled) => {
    var arr = [], finishedFields = [], unFinishedFields = [];
    //是否有保险
    if (!itemInfo || !baseInfo || baseInfo.hasTheftInsurance != "1" || !itemInfo.theftInsuranceInfo) {
        return {
            totalFields: arr,
            finishedFields: finishedFields,
            unFinishedFields: unFinishedFields,
            total: arr.length,
            count: finishedFields.length
        }
    }
    var theftInfo = itemInfo.theftInsuranceInfo;
    arr.push("isFinanceInsurance");
    finishedFields.push("isFinanceInsurance")
    var buyArr = ["increaseFinancePeriod", "increasePriceType", "basePrice", "increasePrice"];//是否融

    let isFinanceInsurance = theftInfo.isFinanceInsurance    // 是否选择盗抢险
    let custPrimaryChannel = baseInfo.custPrimaryChannel              // 030100000线上 030100001线下
    if(custPrimaryChannel === "030100000" && isFinanceInsurance === 1){ // 如果是线上 且 有盗抢险
        arr.push('isGift')
        let isGift = itemInfo.theftInsuranceInfo.isGift
        if(isGift){
            finishedFields.push('isGift')
        }else{
            unFinishedFields.push('isGift')
        }

        if(isGift === '0'){ //选择不赠送
            arr = [...arr, ...buyArr]
            buyArr.forEach(field => {
                if (yx.isEmpty(theftInfo[field])) {
                    unFinishedFields.push(field)
                } else {
                    finishedFields.push(field)
                }
            })
        }
    }else if(custPrimaryChannel === "030100001" && isFinanceInsurance === 1){// 如果是线上 且 有盗抢险
        arr = [...arr, ...buyArr]
            buyArr.forEach(field => {
            if (yx.isEmpty(theftInfo[field])) {
                unFinishedFields.push(field)
            } else {
                finishedFields.push(field)
            }
        })
    }
    
    var obj = {
        totalFields: arr,
        finishedFields: finishedFields,
        unFinishedFields: unFinishedFields,
        total: arr.length,
        count: finishedFields.length
    };
    return obj;
}

//计算其他融资项信息的必填字段
export const getOtherField = (itemInfo, baseInfo, financePeriodYear, isGpsIncreaseItemDisabled) => {
    var arr = [], finishedFields = [], unFinishedFields = [];
    //是否有保险
    if (!itemInfo || !baseInfo || baseInfo.hasOtherFinance != "1" || !itemInfo.otherFinanceInfo) {
        return {
            totalFields: arr,
            finishedFields: finishedFields,
            unFinishedFields: unFinishedFields,
            total: arr.length,
            count: finishedFields.length
        }
    }
    var otherInfo = itemInfo.otherFinanceInfo;//数组
    var neededArr = ["financePrice"];
    var numfields = ["financePrice"]

    otherInfo.forEach((item, i) => {
        let narr = [];
        //不显示gps升级方案时这两个不计入统计
        var gpsIgnore = isGpsIncreaseItemDisabled && (yx.isGpsIncreaseBasePriceCode(item.financeCode) || yx.isGpsIncreaseIncreasePriceCode(item.financeCode));
        if ((yx.isPickerPrice(item.financeType) || yx.isInputPrice(item.financeType))
            && !gpsIgnore) {
            arr.push(item.financeName);
            narr = narr.concat(neededArr);
        }
        narr.forEach(field => {
            if (yx.isEmpty(item[field])) {
                unFinishedFields.push(field + i)
            } else {
                finishedFields.push(field + i)
            }
        })
    })
    var obj = {
        totalFields: arr,
        finishedFields: finishedFields,
        unFinishedFields: unFinishedFields,
        total: arr.length,
        count: finishedFields.length
    };
    // console.log('other字数', obj)
    return obj;
}
//计算贴手续费和贴息信息的必填字段
export const getDiscountField = (discountInfo, isShowPoundage) => {
    var arr = [], finishedFields = [], unFinishedFields = [];
    //是否有保险
    if (!discountInfo || (!isShowPoundage && !discountInfo.hasFeeRule && !discountInfo.hasRateRule)) {
        return {
            totalFields: arr,
            finishedFields: finishedFields,
            unFinishedFields: unFinishedFields,
            total: arr.length,
            count: finishedFields.length
        }
    }
    var hasFeeDiscount = discountInfo.hasFeeRule, hasRateDiscount = discountInfo.hasRateRule;
    const countFn = (row, arr, finishedFields, unFinishedFields) => {
        let newArr = [];
        newArr.push("isDiscount");
        if (row.isDiscount == 1 && row.isDealerDiscount == 1) {
            newArr.push("dealerDiscountAmount");//是否经销商贴手续费
        }
        if (row.isDiscount == 1 && row.isManufacturerDiscount == 1 && row.discountMode == "051400002") {
            //是否厂商贴手续费  自定义时才校验必填
            if(row.customDiscountType === "051400001"){
                newArr.push("ratioValue")
            }else{
                newArr.push("manufacturerDiscountAmount");
            }
        }
        newArr.forEach(field => {
            if (yx.isEmpty(row[field])) {
                unFinishedFields.push(field)
            } else {
                finishedFields.push(field)
            }
        })
        return newArr;
    }
    var feeRow = discountInfo.feeDiscountInfo;
    var rateRow = discountInfo.rateDiscountInfo;
    if (isShowPoundage && !!feeRow) {
        //手续费收取方式
        arr.push("poundageMode");
        if (yx.isEmpty(feeRow.poundageMode)) {
            unFinishedFields.push("poundageMode")
        } else {
            finishedFields.push("poundageMode")
        }
    }
    if (hasFeeDiscount && !!feeRow) {
        var feeArr = countFn(feeRow, arr, finishedFields, unFinishedFields)
        arr = arr.concat(feeArr);
    }
    if (hasRateDiscount && !!rateRow) {
        var rateArr = countFn(rateRow, arr, finishedFields, unFinishedFields)
        arr = arr.concat(rateArr);
    }
    

    var obj = {
        totalFields: arr,
        finishedFields: finishedFields,
        unFinishedFields: unFinishedFields,
        total: arr.length,
        count: finishedFields.length, 
    };
    console.log('discount字数=======', obj)
    return obj;
}

//计算保证金信息的必填字段
export const getMarginField = (marginInfo) => {
    var arr = [], finishedFields = [], unFinishedFields = [];
    if (!!marginInfo) {//自定义
        var marginMethod = yx.getMarginMethod(marginInfo.marginType);
        var neededArr = marginMethod == '1' ? ["marginAmount"] :
            marginMethod == '2' ? ["marginProportion"] :
                marginMethod == '3' ? ["marginAmount_marginProportion"] : [];
        arr = arr.concat(neededArr);
        arr.forEach(field => {
            if (field == "marginAmount_marginProportion") {
                if (yx.isEmpty(marginInfo["marginAmount"]) && yx.isEmpty(marginInfo["marginProportion"])) {
                    unFinishedFields.push(field)
                } else {
                    finishedFields.push(field)
                }
            } else {
                if (yx.isEmpty(marginInfo[field])) {
                    unFinishedFields.push(field)
                } else {
                    finishedFields.push(field)
                }
            }
        })
    }
    var obj = {
        totalFields: arr,
        finishedFields: finishedFields,
        unFinishedFields: unFinishedFields,
        total: arr.length,
        count: finishedFields.length
    };
    // console.log('margin字数=======', obj)
    return obj;
}

//计算贷款试算信息的四个字段
export const getCalcField = (calcInfo, baseInfo) => {
    var arr = [], finishedFields = [], unFinishedFields = [];
    if (!calcInfo || !baseInfo) {
        return {
            totalFields: arr,
            finishedFields: finishedFields,
            unFinishedFields: unFinishedFields,
            total: arr.length,
            count: finishedFields.length
        }
    }
    //尾付计算方式 1金额2比例3自定义
    //var tailCalcMethod = yx.getTailCalcMethod(calcInfo.finalPaymentType);

    if (yx.isEmpty(calcInfo["downPaymentAmount"]) && yx.isEmpty(calcInfo["downPaymentProportion"])) {
        arr.push("downPaymentAmount_downPaymentProportion");
        unFinishedFields.push("downPaymentAmount_downPaymentProportion");
    } else {
        arr.push("downPaymentAmount_downPaymentProportion");
        finishedFields.push("downPaymentAmount_downPaymentProportion");
    }
    if (yx.isEmpty(calcInfo["finalPaymentAmount"]) && yx.isEmpty(calcInfo["finalPaymentProportion"])) {
        arr.push("finalPaymentAmount_finalPaymentProportion");
        unFinishedFields.push("finalPaymentAmount_finalPaymentProportion");
    } else {
        arr.push("finalPaymentAmount_finalPaymentProportion");
        finishedFields.push("finalPaymentAmount_finalPaymentProportion");
    }
    var obj = {
        totalFields: arr,
        finishedFields: finishedFields,
        unFinishedFields: unFinishedFields,
        total: arr.length,
        count: finishedFields.length
    };
    // console.log('calc字数', obj);
    return obj;
}
//计算贷款信息的必填字段
export const getLoanFieldNum = (loan) => {
    var obj = {total: 0, count: 0};
    var financePeriodYear = yx.calcFinanceYear(loan.baseInfo);
    const baseSaveData = TRANS.transToSave('baseInfo', loan);
    let baseObj = getBaseField(baseSaveData)
    let insObj = getItemField(loan, TRANS.transToSave('itemInfo', loan),
        loan.baseInfo, financePeriodYear);
    let marginObj = getMarginField(TRANS.transToSave('marginInfo', loan));
    const financeDiscountInfo = TRANS.transToSave('discountInfo', loan);
    let discountObj = getDiscountField(financeDiscountInfo, loan.baseInfo.isShowPoundage == 1);
    let calcObj = getCalcField(TRANS.transToSave('calcInfo', loan), loan.baseInfo);
    let params = {
        total: baseObj.total + insObj.total + calcObj.total + discountObj.total + marginObj.total,
        count: baseObj.count + insObj.count + calcObj.count + discountObj.count + marginObj.count,
    }
    return params;
}

//模型 中的字段名
export const FIELDS = {}

export default {
    MODEL,
    FIELDS,
}



