<template>
    <div class="sh-steps">
        <div v-for="(item,index) in steps" class="sh-step-cell "
             :class="{ 'sh-step-success': item.step<activeStep,'sh-step-active':activeStep===item.step}">
            <div class="sh-step-content">
                <div class="sh-step-imgicon" :style="getImageStyle(item.step)"></div>
                <span class="sh-step-title">{{(item.step==1 && firstText != '') ? firstText : currentText != '' && activeStep === item.step ? currentText:item.title}}</span>

                <!--<span class="sh-step-complete-icon" v-if="item.index<activeStep">-->
                <!--<Icon type="checkmark-round" size="18"></Icon>-->
                <!--</span>-->
                <!--<span class="sh-step-icon" v-else>{{item.index}}</span>-->
                <!--<span class="sh-step-title">{{(item.index==1 && firstText != '') ? firstText : currentText != '' && activeStep === item.index ? currentText:item.title}}</span>-->
            </div>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 顶部进件分步状态栏
     * @Props: [activeStep: Number] 当前所在步骤
     * @Props: [currentText: String]当前步骤文字
     * @Props: [firstText: String]第一步文字:企业客户在预审页时会修改文字
     */

    export default {
        name: 'applySteps',
        props: {
            activeStep: {
                default: 1
            },
            currentText: {
                default: ''
            },
            firstText: {
                default: ''
            },
        },
        data() {
            return {
                steps: [{
                    step: 1,
                    title: '预审',
                }, {
                    step: 2,
                    title: '提报',
                }, {
                    step: 3,
                    title: '签约',
                }, {
                    step: 4,
                    title: '放款',
                }, {
                    step: 5,
                    title: '完成',
                }],
            }
        },
        mounted() {
        },
        methods: {
            getImageStyle(step) {
                var stats = step == this.activeStep ? '-active' : step < this.activeStep ? '-finished' : '';
                var imgurl = require(`@/assets/img/step/s${step}${stats}.png`);
                return {
                    backgroundImage: `url(${imgurl})`
                }
            },
        },
    }
</script>


