//转换为表单使用格式
export const transEnterpriseToForm = (row) => {
    row = row || {};
    let obj = obj || {}
    const basicRow = row.basicInfo || {};
    const licenseRow = row.licenseInfo || {};
    //合并到obj
    for (let item in MODEL.e_applier) {
        let val = basicRow[item];
        obj[item] = yx.isEmpty(val) ? '' : '' + val;
    }
    for (let item in MODEL.e_license) {
        let val = licenseRow[item];
        obj[item] = yx.isEmpty(val) ? '' : '' + val;
    }
    obj.id = !yx.isEmpty(basicRow.id) ? yx.isEmpty(basicRow.id) : !yx.isEmpty(licenseRow.id) ? licenseRow.id : "";
    obj.orderNo = !yx.isEmpty(basicRow.orderNo) ? yx.isEmpty(basicRow.orderNo) : !yx.isEmpty(licenseRow.orderNo) ? licenseRow.orderNo : "";
    obj.hasSaved = basicRow.hasSaved == true || licenseRow.hasSaved == true;
    //企业注册地省市区   licenseRow
    obj.enterpriseRegistration_Region = {
        "province": obj.enterpriseRegistrationProvince, //省份
        "city": obj.enterpriseRegistrationCity, //城市
        "district": obj.enterpriseRegistrationDistrict, //区/县
        "province_name": obj.enterpriseRegistrationProvinceName, //省份描述
        "city_name": obj.enterpriseRegistrationCityName, //城市描述
        "district_name": obj.enterpriseRegistrationDistrictName, //区/县描述
    }
    //企业经营地省市区  basicRow
    obj.enterpriseManagement_Region = {
        "province": obj.enterpriseManagementProvince, //省份
        "city": obj.enterpriseManagementCity, //城市
        "district": obj.enterpriseManagementDistrict, //区/县
        "province_name": obj.enterpriseManagementProvinceName, //省份描述
        "city_name": obj.enterpriseManagementCityName, //城市描述
        "district_name": obj.enterpriseManagementDistrictName, //区/县描述
    }
    obj.corporationCertificateType = yx.isEmpty(obj.corporationCertificateType) ? '1' : obj.corporationCertificateType;//证件类型默认为1
    obj.corporationCertificateTypeName = yx.isEmpty(obj.corporationCertificateTypeName) ? '身份证' : obj.corporationCertificateTypeName;//证件类型默认为1
    return obj;
}

//企业客户转为保存的数据，只有一个注册资本要转为数字，其他一样
export const transEnterpriseToSave = (row) => {
    row = row || {};
    const enterpriseApplier = {};
    const enterpriseLicense = {};
    FIELDS.enterpriseLicenseFields.forEach(item => {
        let val = row[item];
        if (val === 0) val = 0;
        if (item == 'establishDate' || item == 'certificateExpiryDate') {
            val = val ? new Date(val).format('yyyy-MM-dd') : '';
        }
        enterpriseLicense[item] = val;
    })
    enterpriseLicense.id = row.id || "";
    enterpriseLicense.orderNo = row.orderNo || "";
    enterpriseLicense.hasSaved = row.hasSaved == 1;
    FIELDS.enterpriseApplierFields.forEach(item => {
        let val = row[item];
        if (val === 0) val = 0;
        if (item == 'establishDate' || item == 'certificateExpiryDate') {
            val = dateToString(val)
        }
        enterpriseApplier[item] = val;
    })
    enterpriseApplier.id = row.id || "";
    enterpriseApplier.orderNo = row.orderNo || "";
    enterpriseApplier.hasSaved = row.hasSaved == 1;

    enterpriseLicense.registeredCapital = stringToNumber(enterpriseLicense.registeredCapital);
    enterpriseApplier.corporationShareholdingRatio = stringToNumber(enterpriseApplier.corporationShareholdingRatio);
    if (enterpriseApplier.orderApplicationType == '1') {
        enterpriseApplier.eigenCode = ''; //零售时清空中征码
    }
    if (enterpriseLicense.isLongCertificate == '1') {
        enterpriseLicense.certificateExpiryDate = ''; //长期时清空到期日期
    }
    return {
        enterpriseApplier: enterpriseApplier,
        enterpriseLicense: enterpriseLicense
    };
}


//个人客户转为表单数据
export const transPersonalToForm = (row) => {
    row = row || {};
    let obj = {};
    for (let item in MODEL.applier) {
        let val = row[item];
        obj[item] = yx.isEmpty(val) ? '' : '' + val;
    }
    //单位省市区
    obj.company_Region = {
        "province": obj.companyProvince, //省份
        "city": obj.companyCity, //城市
        "district": obj.companyDistrict, //区/县
        "province_name": obj.companyProvinceName, //省份描述
        "city_name": obj.companyCityName, //城市描述
        "district_name": obj.companyDistrictName, //区/县描述
    }
    //现居地省市区
    obj.currentResidence_Region = {
        "province": obj.currentResidenceProvince, //省份
        "city": obj.currentResidenceCity, //城市
        "district": obj.currentResidenceDistrict, //区/县
        "province_name": obj.currentResidenceProvinceName, //省份描述
        "city_name": obj.currentResidenceCityName, //城市描述
        "district_name": obj.currentResidenceDistrictName, //区/县描述
    }
    //户籍地 省市区
    obj.censusRegister_Region = {
        "province": obj.censusRegisterProvince, //省份
        "city": obj.censusRegisterCity, //城市
        "district": obj.censusRegisterDistrict, //区/县
        "province_name": obj.censusRegisterProvinceName, //省份描述
        "city_name": obj.censusRegisterCityName, //城市描述
        "district_name": obj.censusRegisterDistrictName, //区/县描述
    }
    return obj;
}
